@keyframes shake {
  10%,
  90% {
    transform: translate3d(-1px, 0, 0);
  }
  20%,
  80% {
    transform: translate3d(2px, 0, 0);
  }
  30%,
  50%,
  70% {
    transform: translate3d(-4px, 0, 0);
  }
  40%,
  60% {
    transform: translate3d(4px, 0, 0);
  }
}

@keyframes pulse {
  10% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.2);
  }
  100% {
    transform: scale(1);
  }
}

body {
  font-family: 'Noto Serif', serif;
  background: linear-gradient(to top, #3a3128, #41352b, #483a2e, #503e31, #584235);
  margin: 0;
  font-family: 'Roboto', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  width: 100%;
  color: #ffffff;
}

.app-container {
  width: 100%;
  width: 100vh;
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  padding-bottom: 32px;
}

.logo {
  width: 50%;
  max-width: 150px;
}

.logo-small {
  width: 20%;
  max-width: 80px;
}

h1 {
  text-transform: uppercase;
}

.btn--outline.MuiButton-root {
  color: #e09264;
  border-color: #e09264;
  margin: 4px 8px;
}

.btn--outline-filled.MuiButton-root {
  color: #e09264;
  border-color: #e09264;
  margin: 4px 8px;
  background: #524f4c;
}
.btn--outline-filled.MuiButton-root:hover {
  background: #777370;
}

.home,
.setup {
  flex: 1;
  width: 100%;
  min-height: 90vh;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.game-over {
  flex: 1;
  text-align: center;
}
.game-over .logo {
  margin-top: 32px;
}

.goals__actions {
  text-align: center;
}

.title {
  text-align: center;
}

.subtitle {
  text-align: center;
}

.score-items {
  list-style: none;
  text-align: center;
  margin-left: 0;
}

.header {
  display: flex;
  flex-direction: row;
}
.header.MuiAppBar-colorPrimary {
  background: #e09264;
}
.header--ambush.MuiAppBar-colorPrimary {
  background: #a968d0;
}
.header--ruin.MuiAppBar-colorPrimary {
  background: #777370;
}
.header.MuiAppBar-root {
  flex-direction: row;
  padding: 0 16px;
}
.header.MuiAppBar-root h3 {
  flex: 2;
  text-align: center;
  text-transform: uppercase;
}

.goals-list {
  display: flex;
  padding: 16px 0;
}

.goal-card {
  width: 25%;
  padding: 0 8px;
  display: flex;
  flex-direction: row;
}
.goal-card--inactive {
  opacity: 0.3;
}
.goal-card__types {
  margin-right: 4px;
}

.card {
  width: 100%;
  max-width: 250px;
  border-radius: 10px;
  box-shadow: black 3px 3px 10px;
}
.card--padded {
  margin: 0 16px;
}
.card--faded-left,
.card--faded-right {
  cursor: pointer;
}

.card-container {
  width: 200px;
  position: relative;
}

.card-mini {
  width: 50px;
  border-radius: 5px;
  box-shadow: black 2px 2px 5px;
}
.card-mini--ruin {
  width: 60px;
  left: auto;
  right: -15px;
  top: 25px;
  transform: rotate(15deg);
  border: 2px solid white;
  position: absolute;
}

.card-icon {
  width: 50px;
  height: 50px;
  border-radius: 5px;
}
.card-icon--selected {
  box-shadow: black 2px 2px 5px;
  left: -55px;
  top: 75px;
}
.card-icon--inline-title {
  transform: translateY(13px);
  box-shadow: black 2px 2px 5px;
  margin: 0 8px;
}

.card--faded-left {
  width: 70%;
  opacity: 0.3;
}

.card--faded-right {
  width: 70%;
  opacity: 0.9;
}

.icon {
  width: 100px;
  border-radius: 10px;
  box-shadow: black 2px 2px 5px;
}
.icon--ruin {
  position: absolute;
  top: 45px;
  right: 75px;
  animation: shake 0.8s cubic-bezier(0.36, 0.07, 0.19, 0.97) infinite;
}
.icon--ambush {
  position: absolute;
  right: -15px;
  top: -15px;
  animation: pulse 1.5s infinite ease-in-out;
}

.explore__playarea {
  width: 100%;
  display: flex;
  justify-content: center;
  margin-bottom: 16px;
}
.explore__playarea--with-goals {
  width: 80%;
}

.explore-card {
  text-align: center;
}
.explore-card--current {
  position: relative;
}

#root .bg-ambush {
  background: #68427f;
}

#root .bg-ruin {
  background: #524f4c;
}

.explore-container {
  display: flex;
  width: 100%;
}

.explore-goals {
  width: 20%;
}

.explore-goal {
  display: flex;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 4px;
  margin-bottom: 8px;
  padding: 4px;
  display: flex;
}
.explore-goal--left {
  flex-direction: row;
}
.explore-goal--left .icons {
  padding-right: 8px;
}
.explore-goal--right {
  flex-direction: row-reverse;
  padding-left: 8px;
}
.explore-goal--right .icons {
  padding-left: 8px;
}
.explore-goal--active {
  border: 1px solid white;
}
.explore-goal .effect {
  align-self: center;
}

.rules .content {
  margin-top: 16px;
}

.rule-container {
  background: #ffffff;
  color: #222222;
  padding: 8px 16px 32px;
  margin-bottom: 16px;
  text-align: center;
}

.rule-image {
  width: 50%;
  max-width: 450px;
  margin: 0 auto;
}
.rule-image img {
  width: 100%;
  border-radius: 4px;
  box-shadow: #222222 2px 2px 5px;
}
.rule-image--flat img {
  box-shadow: none;
}

.rule-action-buttons {
  width: 100%;
  display: flex;
  justify-content: center;
  position: sticky;
  left: 0;
  right: 0;
  bottom: 10px;
}
